<template>

  <slider :pages="pages" :sliderinit="sliderinit">
  </slider>  
</template>
 
<script>
import slider from 'vue-concise-slider'// slider
export default {
   // el: '#app',
   data () {
      return {
        //[arr]
        pages:[
          {
            title: '',
            style:{
             background:'url(src/components/img/l1.jpg)'
            }
          },
          {
           title: '',
           style:{
            background:'url(src/components/img/l2.jpg)'
            }
          },
          {
            title: 'slide3',
            style:{
            background:'url(src/components/img/l3.jpg)'
            }
          },
          {
            title: '',
            style:{
             background:'url(src/components/img/l4.jpg)'
 
            }
          },
          {
           title: '',
           style:{
            background:'url(src/components/img/l5.jpg)'
            }
          },
          {
            title: 'slide3',
            style:{
            background:'url(src/components/img/l6.jpg)'
            }
          },
          {
            title: 'slide3',
            style:{
            background:'url(src/components/img/l7.jpg)'
            },
          }


        ],
        //[obj]
        sliderinit: {
          // currentPage: 0,//
          thresholdDistance: 500,//
          thresholdTime: 200,//
          autoplay:2000,//[ms]
          loop:true,//
          direction:' horizontal',//
          infinite:1,//
          slidesToScroll:1,//
        }
      }
    },
    components: {
        slider
    }
}
</script>


