<template>
    <div id="reg">
        <form>
            <ul>
                <li>
                    <input type="text" placeholder="" v-model="data.username">
                </li>
                <li>
                    <input type="text" placeholder="" v-model="yzm">
                    <i v-text="this.code" @click="codes"></i>
                </li>
                <li>
                    <input type="password" placeholder="" v-model="data.password">
                </li>
                <li>
                    <input type="password" placeholder="" v-model="psd">
                </li>
                <li>
                    <input type="text" placeholder="">
                </li>
            </ul>
            <div class="boxx">
                <em @click="reg"></em>
                <p><a href="javascript:;"></a></p>
            </div>
        </form>
        <div class="log">
            <span></span>
        </div>
        <div id="tanchuang" v-show="show">
            <p v-text="cuowu"></p>
            <em @click="yinc"></em>
        </div>
    </div>
</template>

<script>
    import './reg.css'
    import http from '../../../utils/httpclient.js'
    import router from '../../../router/router.js'

    export default{
        data(){
            return {
                code:'',
                data:{
                        username:'',
                        password:''
                    },
                show:false,
                yzm:'',
                cuowu:'',
                psd:''
            }
        },
        methods: {
            reg(){
                let regu = /^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/;
                if(!regu.test(this.data.username)){
                    this.show = true
                    this.cuowu = ''
                    return ;
                }

                if(this.yzm !== this.code){
                    this.show = true
                    this.cuowu = ''
                    return ;
                }

                let regp = /^\S{6,16}$/;
                if(!regp.test(this.data.password)){
                    this.show = true
                    this.cuowu = '6-16'
                    return ;
                }

                if(this.psd !== this.data.password){
                    this.show = true
                    this.cuowu = ''
                    return ;
                }

                http.post('register',this.data).then((res) =>{
                    if(res.status){
                        router.push({name:'login'});
                    } else{
                        this.show = true
                        this.cuowu = ''
                    }
                })
            },
            codes(){
                this.code = ''
                for(var i=0;i<4;i++){
                    var num = parseInt(Math.random()*10);
                    this.code+=num;
                }
            },
            yinc(){
                this.show = false
            }
        }
    }

</script>


