<template>

    <ul>
    <li v-for="obj in menus" :key="obj.path">
    <router-link :to="obj.path">{{obj.text}}</router-link> 
    </li>
    </ul>

</template>
<script type="text/javascript">
    export default{
        data(){
            return{
                menus:[
                    {path:'/fenlei/jinkou',text:''},
                    {path:'/fenlei/guochan',text:''},
                    {path:'/fenlei/jingxuan',text:''},
                    {path:'/fenlei/qinlei',text:''},
                    {path:'/fenlei/haixian',text:''},
                    {path:'/fenlei/jipeng',text:''},
                    {path:'/fenlei/rupin',text:''},
                    {path:'/fenlei/shucai',text:''},
                    {path:'/fenlei/sushi',text:''},
                    {path:'/fenlei/zahuo',text:''},
                    {path:'/fenlei/jiushui',text:''}
                ]
            }
        }
    }
</script>


