<template>
    <div id="dizhi">
        <ul>
            <li>:<input type="text" v-model="profile.receiver"></li>
            <li>:<input type="text" v-model="profile.username"></li>
            <li>:<input type="text" v-model="profile.province"><em class="iconfont icon-icon--"></em></li>
            <li>:<input type="text" v-model="profile.city"><em class="iconfont icon-icon--"></em></li>
            <li>:<input type="text" v-model="profile.region"><em class="iconfont icon-icon--"></em></li>
            <li>:<input type="text" v-model="profile.address"></li>
            <li>:<input type="radio" id="gs" name="duo"><label for="gs"></label>
                         <input type="radio" id="jt" name="duo"><label for="jt"></label>
            </li>
            <li><i></i></li>
        </ul>

        <span @click="btn_save"></span>
    </div>
</template>

<script>
    import './dizhi.css'
    import http from '../../../../utils/httpclient.js'
    import router from '../../../../router/router.js'

    export default{
        data(){
            return {
                profile:{
                    receiver:'',
                    username:'',
                    province:'',
                    city:'',
                    region:'',
                    address:''
                }
            }
        },
        methods: {
            btn_save(){
                http.post('update',this.profile).then((res) =>{
                    if(res.status){
                        window.localStorage.setItem('mz',res.data.receiver);
                        window.localStorage.setItem('dizhi',res.data.address);

                        router.push({name:'jiesuan'})
                    }
                })
            }
        }
    }


</script>


