<template>
    <div id="jiesuanl">
        <router-link to='/dizhi'><div class="diz">
            <p v-show="show"><i class="iconfont icon-wodeshouhuodizhi"></i><em class="iconfont icon-icon--"></em></p>
            <p v-show="yshow"><i class="iconfont icon-wodeshouhuodizhi"></i>{{dizhi}} {{mz}}<em class="iconfont icon-icon--"></em></p>
        </div></router-link>

        <div class="shangp">
            <p><span>7</span><em class="iconfont icon-icon--"></em></p>
            <ul>
                <li><img src="../../img/a1.png"></li>
                <li><img src="../../img/a2.png"></li>
                <li><img src="../../img/a3.png"></li>
                <li><img src="../../img/a4.png"></li>
            </ul>
        </div>

        <div class="peis">
            <p><span>09:00~21:00</span><em class="iconfont icon-icon--"></em></p>
            <p><span></span><em class="iconfont icon-icon--"></em></p>
            <p>:<input type="text" placeholder=""></p>
            <p class="p1"><span></span></p>
        </div>

        <div class="heji">
            <ul>
            <li><span>757.60</span></li>
            <li><span>10.00</span></li>
            <li><span>767.60</span></li>
            <li>U<i>i</i><span>U</span></li>
            </ul>
        </div>

        <div class="foot">
            <p>:<span>767.60</span></p>
            <em></em>
        </div>

    </div>
</template>

<script>
    import './jiesuan.css'

    export default{
        data(){
            return {
                dizhi:'',
                mz:'',
                show:true,
                yshow:false
            }
        },
        mounted(){
            this.dizhi = window.localStorage.getItem('dizhi')
            this.mz = window.localStorage.getItem('mz')
            console.log(this.dizhi)
            if(this.dizhi == null || this.dizhi == ''){
                this.show = true
                this.yshow = false
            } else {
                this.show = false
                this.yshow = true
            }
        }
    }


</script>


